body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.post{ 
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.post h1{
  color: wheat;
}
.back{
  font-size: 30px;
  color: aliceblue;

}
.post p{
  width: 350px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 22px;
  color: rgba(245, 222, 179, 0.719);
}

body{
  background-color: #282c34;
  display: flex;
  justify-content: center;
}

.login {
  display: flex;
  margin-top: 30%;
  width: 550px;
  align-content: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-animation: login 1 2s;
          animation: login 1 2s;
}

@-webkit-keyframes login {
  0%{
    opacity: 0%;
  }
  50%{
    opacity: 70%;
  }
  70%{
    opacity: 100%;
  }
}

@keyframes login {
  0%{
    opacity: 0%;
  }
  50%{
    opacity: 70%;
  }
  70%{
    opacity: 100%;
  }
}
.login h1{
  color: wheat;
  -webkit-animation: tex 1 0.5s;
          animation: tex 1 0.5s;
}
@-webkit-keyframes tex {
  0%{
    font-size: 20px;
    text-align-last: 15px;
    opacity: 0%;
  }
  50%{
    font-size: 50px;
    text-align-last: 25px;
  }
}
@keyframes tex {
  0%{
    font-size: 20px;
    text-align-last: 15px;
    opacity: 0%;
  }
  50%{
    font-size: 50px;
    text-align-last: 25px;
  }
}
.logout{
  position: absolute;
  right: 0;
  top: 0;
  margin: 5px;
}

.postlists h1 {
  color: wheat;
}
.postlists ul{
  color: rgba(245, 222, 179, 0);
}
.postlists a{
  font-size: 22px;
  color: rgba(245, 222, 179, 0.787);
}

.parag{
  display: flex;
  align-items: center;
  text-align: center;
}

.comments h1{
  color: rgb(245, 222, 179);
}

.comments li{
  color: #e1fb61f4;
}
.comments ul{
  list-style-type:number;
  text-align: center;
}
